  justify-content: center;
  align-content: center;
}
.blog-container {
  padding: 2rem 0;
  width: 60vw;
  font-size: 1.2rem;
  font-weight: 500;
  font-family: 'Neue Radial B Medium';
}
.blog-container > img {
  width: 100%;
}
.bold {
  font-weight: bold;
}
.italic {
  font: italic;
}
.bg-subheading {
  font-weight: bold;
  font-size: 1.6rem;
}
strong {
  font-weight: 600;

}
.blog-title {
